    transform: scale(.9);
  }

  10% {
    transform: scale(.95);
  }

  20% {
    transform: scale(1);
  }

  30% {
    transform: scale(1.05);
  }

  40% {
    transform: scale(1.1);
  }

  50% {
    transform: scale(1.15);
  }

  60% {
    transform: scale(1.1);
  }

  70% {
    transform: scale(1.05);
  }

  80% {
    transform: scale(1);
  }

  90% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

